body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.jumbotron {
  background: -webkit-gradient(linear, left top, right top, from(#08ffea), to(#48e484));
  background: -webkit-linear-gradient(left, #08ffea, #48e484);
  background: -o-linear-gradient(left, #08ffea, #48e484);
  background: linear-gradient(to right, #08ffea, #48e484);
  border-radius: 0px!important;
  margin-top: -20px;
}

.searchForm {
  height: 80px;
  font-size: 50px;
}

.searchBtn {
  height: 80px;
  width: 200px;
}

.btn {
  margin-left: 5px;
}

.sortBtn {
  margin: 20px;
}

.active {
  background-color: #08ffea;
  padding: 18px 10px 18px 10px; 
}


